.projects {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 3rem;
}

.container {
    display: flex;
    flex-wrap: wrap;
    gap: 4rem;
    margin-top: 2rem;
    align-items: center;
    justify-content: center;
    padding-top: 1rem;

}


.projectContainer {
    width: 500px;
    border: rgb(163, 163, 163) 0.1rem solid;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 2rem;
    cursor: pointer;

}

.projectContainer img {
    width: 500px;
    border-radius: 2rem;
    margin: 1rem;
    transition: transform 0.3s;
    
}

.projectContainer img:hover{
    transform: scale(1.2);
    border: rgb(163, 163, 163) 0.1rem solid;
    border-radius: 0.5rem;
}




.buttons {
    display: flex;
    margin-bottom: 1rem;
    gap: 2rem;
}

.buttons button {
    width: 8rem;
    padding: 1rem;
    border-radius: 2rem;
    transition: all 300ms ease;
    font-weight: 600;
    cursor: pointer;

}

.buttons button:hover {
    background-color: rgb(53, 53, 53);
    color: white;
}

.icon {
    cursor: pointer;
    height: 2rem;
}

.Arrow{
    align-self: flex-end;
    margin-top: 1rem;
    margin-right: 2rem;
}

.description {
    display: flex;
    flex-direction: column;
    margin-bottom: 0.5rem;
    gap: 0.5rem;
    margin: 0.5rem;
}


@media (max-width: 550px) {
    .projectContainer{
        width: 400px;
      }


    .projectContainer img{
        width: 400px;
        margin: auto;
    }


    
}



@media (max-width: 500px){
 
    .projectContainer{
      width: 300px;
    }

    .projectContainer img {
        width: 300px;
    }

    .projectContainer img:hover{
        transform: scale(1);
        border: none;
        border-bottom: 2px solid black;
    }
  
    .Arrow{
      display: none;
    }
  
  
  }